
import VHeader from '@/components/v-header/v-header'
import { isIOS, isIphoneX } from '@/assets/js/brower'
import Masker from 'vux/src/components/masker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import axios from '@/api/axiosApi'
import Scroll from '@/components/scroll/scroll'
import { localUser } from '@/assets/js/local'
export default {
data () {
return {
styles: {
height: '0px'
},
collectionList: [],
user: {},
show: false,
collectionId: '',

showMap: false,
menusMap: {},
showGaoDei: false,

latitude: '',
longitude: '',
address: '',
showCancel: true
}
},
methods: {
handleClickProductDesc (id, type) {
this.$router.push('/address/product/' + id + '/' + type)
},

getCollectionList (isFirst) {
const userId = localUser.get('id')
axios.get(this, '/v1/collection/' + userId, {pageNum: this.pageNum, pageSize: this.pageSize}, (data) => {
data.forEach(item => {
item.imgUrl = item.atlas.split(';')[0]
})
if (isFirst) {
this.collectionList = data
return
}
if (data.length === 0) {
this.$vux.toast.text('没有更多数据了', 'middle')
if (this.$refs.scroll && this.pageNum > 0) {
this.$refs.scroll.hiddenPullUpLoad()
} else {
this.$refs.scroll.hiddenPullDown()
}
} else {
if (this.pageNum > 1) {
this.collectionList = this.collectionList.concat(data)
} else {
this.collectionList = data
}
}
})
},
handleAlert (id) {
this.show = true
this.collectionId = id
},
handleClickCollectionIcon (id) {
axios.put(this, '/v1/messageRecord', {id: id, collectionType: 0}, (data) => {
this.$vux.toast.text('取消收藏成功', 'middle')
this.collectionList.forEach(item => {
if (item.id === id) {
item.collectionType = 0
}
})
})
},
onPullingUp () {
this.pageNum++
this.getCollectionList()
},
onPullingDown () {
this.pageNum = 1
this.getCollectionList()
},


onCancel () {
},
onConfirm () {
this.handleClickCollectionIcon(this.collectionId)
},


goToMap (latitude, longitude, address) {
if (isIOS()) {
window.webkit.messageHandlers.NativeMethod.postMessage({'methodName': 'openMapNavi', 'latitude': latitude, 'longitude': longitude})
} else {

this.menusMap = JSON.parse(window.android.isInstallByread())
if (this.menusMap == null || Object.getOwnPropertyNames(this.menusMap).length === 1) {
this.showCancel = false
this.showGaoDei = true
return
}
if (this.menusMap.baiDu === '2') {
this.menusMap.baiDu = '百度地图'
}
if (this.menusMap.gaoDei === '1') {
this.menusMap.gaoDei = '高德地图'
}
if (this.menusMap.tenXun === '3') {
this.menusMap.tenXun = '腾讯地图'
}
this.showMap = true
this.latitude = latitude
this.longitude = longitude
this.address = address
}
},

handleClickMenu (value) {
if (value === 'baiDu') {
window.android.makeUpMap(this.latitude, this.longitude, this.address, 2, 1, '')
} else if (value === 'gaoDei') {
window.android.makeUpMap(this.latitude, this.longitude, this.address, 1, 1, '')
} else if (value === 'tenXun') {
window.android.makeUpMap(this.latitude, this.longitude, this.address, 3, 1, '')
}
},

onCancelGaoDei () {
},
onConfirmGaoDei () {
window.android.makeUpMap(this.latitude, this.longitude, this.address, 0, 1, '')
}
},
components: {
VHeader,
Masker,
Flexbox,
FlexboxItem,
Scroll,
Rater,
Confirm,
isIOS,
isIphoneX,
Actionsheet
},
mounted () {
this.styles.height = window.innerHeight - 64 + 'px'
},
created () {
this.pageNum = 1
this.pageSize = 10
this.getCollectionList(true)
}
}



